<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isshow">
      <el-form :model="form">
        <el-form-item label="一级分类" label-width="120px">
          <el-select v-model="form.first_cateid" @change="changeFirstid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option v-for="item in classifyList" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级分类" label-width="120px">
          <el-select v-model="form.first_cateid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option label="家用电器" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称" label-width="120px">
          <el-input v-model="form.specsname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="价格" label-width="120px">
          <el-input v-model="form.specsname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="市场价格" label-width="120px">
          <el-input v-model="form.specsname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="图片" label-width="120px">
          图片
        </el-form-item>
        <el-form-item label="商品规格" label-width="120px">
          <el-select v-model="form.first_cateid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option label="家用电器" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="规格属性" label-width="120px">
          <el-select v-model="form.first_cateid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option label="家用电器" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否新品" label-width="120px">
          <el-radio v-model="form.type" :label="1">是</el-radio>
          <el-radio v-model="form.type" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="是否热卖" label-width="120px">
          <el-radio v-model="form.type" :label="1">是</el-radio>
          <el-radio v-model="form.type" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="状态" label-width="120px">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item label="状态" label-width="120px">
          <textarea name="" id="" cols="30" rows="10"></textarea>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.title=='商品规格添加'">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {mapActions,mapGetters} from 'vuex'
import { alertSuccess, alertWarning } from "../../../util/alert";
export default {
  props: ["info"],
  computed: {
      ...mapGetters({
          classifyList:'classify/classify'
      })
  },
  components: {},
  data() {
    return {
      form: {
        first_cateid:'',
        second_cateid:'',
        goodsname:'',
        price:'',
        market_price:'',
        img:null,
        description:'',
        specsid:'',
        specsattr:[],
        isnew:1,
        ishot:1,
        status:1,
      }
    };
  },
  methods: {
      ...mapActions({
          requsetClassifyList:'classify/requsetClassifyList'
      }),
      changeFirstid(){

      },
    add() {},
    update() {},
    cancel() {}
  },
  mounted() {
      this.requsetClassifyList()
  }
};
</script>
<style scoped>
</style>


