<template>
<div>
    <el-button type="primary" @click="willAdd">添加</el-button>
    <v-list></v-list>
    <v-add :info='info'></v-add>
</div>
</template>
<script>
import vList from './components/list.vue'
import vAdd from './components/add.vue'
export default {
components:{
    vList,
    vAdd
 },
data () {
 return {
     info:{
         isshow:false,
         title:'商品管理添加'
     }
 }
},
methods:{
    willAdd(){
        this.info={
         isshow:true,
         title:'商品管理添加'
     }
    }
},
mounted(){
}
}
</script>
<style scoped>
</style>


