<template>
  <div>
    <el-table
      :data="list"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      border
      :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
    >
      <el-table-column prop="id" label="商品编号" width="110px"></el-table-column>
      <el-table-column prop="goodsname" label="商品名称" width="110px"></el-table-column>
      <el-table-column label="图片">
        <template slot-scope="scope">
          <img :src="$imgPre+scope.row.img" />
        </template>
      </el-table-column>
      <el-table-column label="是否新品">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.isnew===1"> 是 </el-button>
          <el-button type="danger" v-else> 否 </el-button>
        </template>
      </el-table-column>
      <el-table-column label="是否热卖">
          <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.ishot===1"> 是 </el-button>
          <el-button type="danger" v-else> 否 </el-button>
        </template>
      </el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status===1">启用</el-button>
          <el-button type="info" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删 除</el-button>
          <!-- <del-btn @confirm="del(scope.row.id)"></del-btn> -->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters({
      list: "goods/list"
    })
  },
  components: {},
  data() {
    return {};
  },
  methods: {
    ...mapActions({
      requsetGoodsList: "goods/requsetGoodsList"
    })
  },
  mounted() {
    this.requsetGoodsList();
  }
};
</script>
<style scoped>
img {
  width: 100px;
  height: 100px;
}
</style>


